const lazyLoadingData = function(element) {
    return new Promise((resolve, reject) => {
        try {
            let query = null;

            query = this.createSelectorQuery();




            query.select(`.${element}`).boundingClientRect();
            query.exec(res => {
                resolve(res);
            })
        } catch(err) {
            reject(err);
        }
    })
};

export default lazyLoadingData;
